<script>
	export let icon;
	export let onClick;
	let cls; export {cls as class};
</script>
<div on:click={onClick} class="waves-effect {cls} btn">
	{#if icon && icon !== ""}
		<i class="material-icons left">{icon}</i>
	{/if}
	
	<span class={icon && icon !== ""?"":"adjust"}><slot></slot></span>

</div>